import React, { useState, useEffect } from 'react'
import './opacityChanger.css';

export const OpacityChanger = ({ allMapsRefs, mainTileSet, tilesetForComparation }) => {
    const [mainMapRef, beforeMapRef, afterMapRef] = allMapsRefs;
    const [isMainSliderActive, setIsMainSliderActive] = useState(false);
    const [isBeforeSliderActive, setIsBeforeSliderActive] = useState(false);
    const [isAfterSliderActive, setIsAfterSliderActive] = useState(false);

    useEffect(() => {
        activateSliders(mainMapRef, mainTileSet, setIsMainSliderActive);
    }, [mainTileSet, mainMapRef])

    useEffect(() => {
        activateSliders(beforeMapRef, mainTileSet, setIsBeforeSliderActive);     
    }, [mainTileSet, beforeMapRef])

    useEffect(() => {
        activateSliders(afterMapRef, tilesetForComparation, setIsAfterSliderActive);
    }, [tilesetForComparation, afterMapRef])

    const activateSliders = (mapRef,tileset,setActive) => {
        if (mapRef) {
            if (tileset.children !== null) {
                setActive(true);
            } else {
                setActive(false);
            }
        } else {
            setActive(false);
        }
    }


    return (
        <div className="fixed bottom-0 mb-20 w-full flex z-20 ">
            <div className="mr-60"></div>
            <div className=" flex  w-full id slider-container justify-around">
                <Slider mapRef={mainMapRef} isSliderActive={isMainSliderActive}  tileset={mainTileSet} />
                <Slider mapRef={beforeMapRef} isSliderActive={isBeforeSliderActive} tileset={mainTileSet} />
                <Slider mapRef={afterMapRef} isSliderActive={isAfterSliderActive} tileset={tilesetForComparation} />
            </div>
        </div>
    )
}

const Slider = ({ mapRef, isSliderActive,tileset }) => {
    const [sliderTextValue,setSliderTextValue] = useState(100)
    const [sliderValue,setSliderValue] = useState(100)
    const changeSliderValue = (e) =>{
        const intValue = e.target.value;        
        setSliderValue(intValue)
        setSliderTextValue(parseInt(intValue, 10))
        mapRef.current.setPaintProperty(            
            tileset.children[0].id,
            'raster-opacity',
            parseInt(intValue, 10) / 100
        );                     
    }

    return (
        <div className={`map-overlay top ${mapRef ? 'block' : ' hidden'}`}>
            <div className={`map-overlay-inner text-center w-60 ${isSliderActive ? ' block' : ' hidden'}`}>
                <label>Opacidad del camino: <span id="slider-value">{sliderTextValue}%</span></label>
                <input id="slider" type="range" min="0" value={sliderValue} max="100" step="0" onChange={changeSliderValue} />
            </div>
        </div>
    )
}