import React, { useState } from "react";
import { FaAngleUp, FaAngleDown } from "react-icons/fa";
import tren_maya from "../components/vectors/tren_maya.png";


export const SecondaryTilesetList = ({
  setTilesetComparation,
  tilesetForComparation,
  mainTileSet,
  tilesetList,
  selectedRegion,
}) => {
  const [ isCompareTilesetRadioListVisible, setIsCompareTilesetRadioListVisible, ] = useState(true);
  const [isCompareTilesetRadiosEnabled, setIsCompareTilesetRadiosEnabled] = useState(true);
  

  const changeTileset = (tileset) => {
    setIsCompareTilesetRadiosEnabled(false);
    setTilesetComparation(tileset);
    setTimeout(() => {
      setIsCompareTilesetRadiosEnabled(true);
    }, 500);
  };

  return (
    <div className="z-10 bg-white text-black  rounded-r-2xl  w-96 py-2">
      <div className="flex flex-col justify-end">
        <div className="flex flex-row">
          <div className="flex text-black ml-8">
            <img src={tren_maya} className="h-2/3 my-auto" alt="" />
          </div>
          <div className="flex text-black my-auto w-3/4">
            <p className="font-bold">
              {selectedRegion.lng}&#176;N, &nbsp; {selectedRegion.lat}&#176; W
            </p>
          </div>
        </div>
        <hr />
        <div className="flex flex-row py-4">
          <div className="flex flex-col flex-1">
            <div className="flex mx-auto">
              <h1> Punto de observación</h1>
            </div>
            <div className="flex mx-auto text-xs"> {selectedRegion.name}</div>
          </div>
          <div className="flex flex-1">
            <div className="flex flex-col flex-1">
              <div className="flex mx-auto">
                <h1> Tipo de Espectro</h1>
              </div>
              <div className="flex mx-auto text-xs"> NDVI</div>
            </div>
          </div>
        </div>
        <hr />
        <div className="flex justify-end">
          {tilesetList.length < 1 ? (
            <p className="mx-auto py-4">Seleccione un Punto de Observación</p>
          ) : (
            <>
              <button
                className="bg-color5 mx-auto text-white my-4 p-2 rounded-xl flex "
                onClick={() => setIsCompareTilesetRadioListVisible((v) => !v)}
              >
                <div className="mr-3 text-white "> Seleccione fecha que desea comparar</div>
                <div className="mt-1 mr-2">
                  {isCompareTilesetRadioListVisible ? (
                    <FaAngleUp />
                  ) : (
                    <FaAngleDown />
                  )}
                </div>
              </button>
            </>
          )}
        </div>
        {isCompareTilesetRadioListVisible ? (
          <ul className="px-2 bg-white  rounded mx-auto mb-4">
            {tilesetList.map((tileset) => (
              <li className="flex" key={Math.random()}>
                <label className="flex">
                  <input
                    type="radio"
                    className="mt-2 -mr-1"
                    name="comparisonTileSet"
                    id={tileset.id}
                    value={tileset.id}
                    checked={tilesetForComparation?.id === tileset.id}
                    onChange={() => changeTileset(tileset)}
                    disabled={!isCompareTilesetRadiosEnabled}
                  />
                  <p> &#160; {tileset.name}</p>
                </label>
              </li>
            ))}
          </ul>
        ) : (
          ""
        )}
      </div>
    </div>
  );
};
