import React, { useState } from "react";
import mapStyles from "./../../../../data/mapStyles";

const BaseMapModal = (props) => {
  //TODO cambiar las imagenes a lugares de mexico
  //si se cambia muy rapido, crashea, deshabilitar los botones un rato despues del click
  const [areRadiosDisabled, setAreRadiosDisabled] = useState(false);
  const handleStyleChange = (e) => {
    setAreRadiosDisabled(true);
    props.changeBaseMap(e.target.id);
    setTimeout(() => {
      setAreRadiosDisabled(false);
    }, 600);
  };
  const MapOption = ({ image, title, value, id }) => {
    return (
      <div
        className={`p-1 rounded  ${id === props.mainBackgroundStyle ? "bg-black bg-opacity-10" : ""}`}
      >
        {" "}
        {/*Si el id es igual al mapa seleccionado, cuadro azul*/}
        <div className={`p-1`}>
          <label>
            <img
              src={image}
              alt=""
              className="w-32 object-cover rounded-lg mb-1"
            />
            <div className="text-center">
              <input
                className="hidden"
                disabled={areRadiosDisabled}
                type="radio"
                name="basemap"
                id={id}
                value={value}
                onChange={(e) => {
                  handleStyleChange(e);
                }}
              />
              &#160; {title}
            </div>
          </label>
        </div>
      </div>
    );
  };
  return (
    <div className="bg-white rounded-r-2xl p-6 z-30 w-max text-black">
      <div>
        <h1 className="text-left mb-2 text-xl">
          Seleccione un tipo de mapa base:
        </h1>
      </div>
      <div className="">
        <div
          className="grid grid-cols-2 md:grid-cols-3  gap-4"
          onChange={() => { }}
        >
          {mapStyles.map(({ title, image, value, id }) => (
            <MapOption
              title={title}
              image={image}
              value={value}
              key={id}
              id={id}
            />
          ))}
        </div>
      </div>
    </div>
  );
};

export default BaseMapModal;
